.expenseFormCard {
  min-height: 30rem;
}
.expenseFormCard .formCard {
  display: inherit;
}
.expenseFormCard .formCard .hide {
  display: none;
}
.expenseFormCard .formCard form {
  display: flex;
  flex-flow: column;
  gap: 1rem;
}
.expenseFormCard .formCard form .formControls {
  display: flex;
  flex-flow: column;
  gap: 1rem;
}
.expenseFormCard .formCard form .formControls .formControl input {
  border: 7px solid rgba(230, 230, 230, 0.3);
  height: 2.5rem;
  border-radius: 7px;
  padding: 1rem;
}
.expenseFormCard .formCard form .formActions {
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
}